import { DataSearchResult } from "../../data/types";
import { CardHeader, StyledCard } from "./styles";

const Card = ({data}: { data: DataSearchResult }) => {

    return <StyledCard>
        <CardHeader>{data.title}</CardHeader>
        <p style={{fontSize: "0.7em"}}>(Relevance: {data.score.toFixed(2)})</p>
        <div>{data.body}</div>
    </StyledCard>
}

export default Card;