import { t } from "@utils/i18n";
import { FormEventHandler } from "react";
import { SearchParagraph, StyledSearchBar, StyledSearchForm } from "./styles";

interface SearchBarProps {
    onInput: FormEventHandler<HTMLInputElement>
    initialValue: string
}

const SearchBar = ({onInput, initialValue}: SearchBarProps) => {

return <StyledSearchForm>
        <SearchParagraph>{t("intro")}</SearchParagraph>
        <StyledSearchBar type="text" onInput={onInput} value={initialValue} />
    </StyledSearchForm>
}

export default SearchBar;